.button{
  display: block;

  width: 100%;
  max-width: 165px;
  height: 39px;
  border: 2px solid $orange;
  background: inherit;

  text-align: center;
  font-size: 16px;
  line-height: 21px;

  padding: calc((35px - 21px) / 2) 0;
  margin: 0 auto;
  transition: all 0.4s ease-out;
}