.skills{
  &__wrap{
    margin: 0 auto;
    max-width: 968px;
  }
  //&__title{}
  &__items{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  &__item{
    display: flex;
    align-items: center;
    column-gap: 10px;
    img{
      max-width: 27px;
      max-height: 31px;
    }
  }
}

@media screen and (min-width: 320px){
  .skills{
    &__wrap{
      padding: 35px 0;
    }
    &__items{
      column-gap: calc(48px * 100% / 753px);
      row-gap: 24px;
    }
    &__item{
      span{
        font-size: 16px;
        line-height: 21px;
      }
    }
  }
}

@media screen and (min-width: 481px){
  .skills{
    &__wrap{
      padding: 40px 0;
    }
    &__items{
      row-gap: 30px;
    }
    &__item{
      span{
        font-size: 20px;
        line-height: 26px;
      }
    }
  }
}

@media screen and (min-width: 769px){
  .skills{
    &__wrap{
      padding: 55px 0;
    }
    &__items{
      row-gap: 50px;
    }
    &__item{
      span{
        font-size: 24px;
        line-height: 31px;
      }
    }
  }
}

@media screen and (min-width: 1000px){
  .skills{
    &__wrap{
      padding: 60px 0;
    }
    &__items{
      column-gap: calc(100px * 100% / 983px);
    }
  }
}

@media screen and (min-width: 1171px) {
  .skills{
    &__items{
      column-gap: 106px;
    }
  }
}

