.about{
  &__content{
    max-width: 970px;
    margin: 0 auto;

    display: flex;
    gap: 34px;
  }
  &__img{
    img{
      object-fit: cover;
      object-position: top;
      width: 100%;
      height: 100%;
    }
  }
  //&__inner{}
  &__txt{
    font-size: 24px;
    line-height: 150%;
    &:first-child{
      margin-bottom: 16px;
    }
  }
  &__achivs-items{
    display: flex;
    justify-content: space-between;
    max-height: 90px;
    max-width: 510px;
    width: 100%;
  }
  &__item{
    color: $blue;
    text-align: center;
  }
}

@media screen and (min-width: 320px){
  .about{
    &__wrap{
      padding: 35px 0;
    }
    &__title{
      margin-bottom: 30px;
    }
    &__content{
      flex-direction: column;
      align-items: center;
      gap: 20px;
    }
    &__img{
      max-width: 340px;
      max-height: 400px;
      flex: 1 1 auto;
      transition: all 0.6s ease;
    }
    &__inner{
      text-align: center;
      flex: 1 1 auto;
    }
    &__txt,  &__item{
      font-size: 16px;
      line-height: 24px;
    }
    &__txt{
      &:first-child{
        margin-bottom: 12px;
      }
    }
    &__item{
      &:nth-child(2){
        min-width: 122px;
      }
      span{
        font-size: 32px;
        line-height: 48px;
      }
    }
  }
}

@media screen and (min-width: 481px){
  .about{
    &__wrap{
      padding: 40px 0;
    }
    &__title{
      margin-bottom: 40px;
    }
    &__img{
      max-width: 440px;
      max-height: 509px;
    }
    &__txt, &__item{
      font-size: 20px;
      line-height: 30px;
    }
    &__item span{
      font-size: 40px;
      line-height: 60px;
    }
  }
}

@media screen and (min-width: 769px){
  .about{
    &__wrap{
      padding: 55px 0;
    }
    &__title{
      margin-bottom: 50px;
    }
    &__content{
      flex-direction: row;
      flex-wrap: wrap;
      align-items: flex-start;
      column-gap: 14px;
      row-gap: 30px;
    }
    &__img{
      align-self: stretch;
      flex: 1 1 calc((100% - 14px) / 2);
    }
    &__inner{
      text-align: start;
      flex: 1 1 calc((100% - 14px) / 2);
    }
    &__txt{
      font-size: 24px;
      line-height: 36px;
      &:first-child{
        margin-bottom: 16px;
      }
    }
    &__achivs-items{
      margin: 0 auto;
    }
    &__item{
      font-size: 20px;
      line-height: 30px;
      &:nth-child(2){
        min-width: 153px;
      }
      span{
        font-size: 40px;
        line-height: 60px;
      }
    }
  }
}

@media screen and (min-width: 1000px){
  .about{
    &__wrap{
      padding: 60px 0;
    }
    &__img{
      max-width: 370px;
    }
    &__txt{
      margin-bottom: 40px;
    }
    &__achivs-items{
      margin: 0;
    }
  }
}

