.proj{
   &__wrap{
      max-width: 968px;
      width: 100%;
   }
   //&__title{}
   &__items{
      display: flex;
      flex-direction: column;
   }
   &__item{
      max-width: 968px;
   }
   &__item-img{
      position: relative;
   }
   &__img-laptop{
      width: calc(892px * 100% / 968px);
      padding-top: calc(514px * 100% / 968px);
      max-width: 892px;
      max-height: 514px;

      position: relative;
      img{
         position: absolute;
         top: 0;
         left: 0;

         z-index: 2;
      }
      img:nth-child(2){
         top: 28px;
         left: 50%;
         transform: translateX(-50%);
         z-index: 1;
      }
   }
   &__img-phone{
      position: absolute;
      bottom: calc(-25px * 100% / 514px);
      right: 0;
      z-index: 2;

      width: calc(202px * 100% / 968px);
      max-width: 202px;
      max-height: 411px;
   }
   &__item-txt{
      line-height: 150%;
      text-align: center;
      mark{
         background: $orange;
      }
   }
   &__button{
      display: block;
   }
   &__img-phone, &__img-laptop{
      overflow: hidden;
      img{
         max-width: 100%;
         height: auto;
      }
   }
   &__wrap, &__button{
      margin: 0 auto;
   }
}

@media screen and (min-width: 320px){
   .proj{
      &__wrap{         
         padding: 35px 0;
      }
      &__items{
        row-gap: 40px;
      }
      &__item-txt{
        font-size: 16px;
        margin-bottom: 20px;
      }
      &__item-img{
        margin-bottom: 29px;
      }
   }
}
@media screen and (min-width: 481px){
   .proj{
      &__wrap{         
         padding: 40px 0;
      }
      &__items{
        row-gap: 50px;
      }
      &__item-img{
        margin-bottom: 43px;
      }
      &__item-txt{
        font-size: 20px;
        margin-bottom: 28px;
      }
   }
}
@media screen and (min-width: 769px){
   .proj{
      &__wrap{         
         padding: 50px 0 45px;
      }
      &__items{
        row-gap: 70px;
      }
      &__item-img{
        margin-bottom: 62px;
      }
      &__item-txt{
        font-size: 24px;
        margin-bottom: 36px;
      }
   }
}
@media screen and (min-width: 1000px){
   .proj{
      &__wrap{         
         padding: 60px 0 40px;
      }
      &__items{
        row-gap: 80px;
      }
      &__item-img{
        margin-bottom: 68px;
      }
   }
}
@media screen and (min-width: 1171px){
   .proj{
      &__item-img{
         margin-bottom: 69px;
      }
   }
}
