@import './vendor/normalize';
@import './base/variables';
@import './base/fons';

*, *::before, *::after{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*li{
  list-style-type: none;
}

*a{
  text-decoration: none;
  color: inherit;
}

[class*="__container"]{
  max-width: 1230px;
  margin: 0 auto;
}

html, body{
  min-height: 100%;
  font-family: 'Chakra Petch', sans-serif;
  font-weight: 400;
}

body{
  display: flex;
  flex-direction: column;
  background: #121316;
  color: $white;
}

@import './base/title';
@import './base/button';

//Main BEM blocks
@import './header/header';
@import './main/main';
@import './footer/footer';

//Styles for adaptive
@import './base/media';
